// <script>
  
  let coerceBoolean = (val) => (typeof val !== "string" ? val :
  val === "true" ? true :
  val === "false" ? false :
  val === "null" ? false :
  val === "undefined" ? false : val)

  export default {
    props: {
      options: {
        type: Array,
        default() { return [] },
      },
      value: {
        twoWay: true
      },
      placeholder: {
        type: String,
        default: '--'
      },
      multiple: {
        type: Boolean,
        coerce: coerceBoolean,
        default: false
      },
      search: { // Allow searching (only works when options are provided)
        type: Boolean,
        coerce: coerceBoolean,
        default: false
      },
      searchPlaceholder: {
        type: String,
        default: 'Search'
      },
      limit: {
        type: Number,
        default: 1024
      },
      closeOnSelect: { // only works when multiple==false
        type: Boolean,
        coerce: coerceBoolean,
        default: false
      },
      disabled: {
        type: Boolean,
        coerce: coerceBoolean,
        default: false
      },
      canAllSelect: {
        type: Boolean,
        coerce: coerceBoolean,
        default: false
      },
      fetchOption: {
        type: Function,
        default () {
          return ()=>{}
        }
      },
      isAjaxOption: {
        type: Boolean,
        coerce: coerceBoolean,
        default: false
      },
      isAjaxPending: {
        type: Boolean,
        coerce: coerceBoolean,
        default: false
      },
      itemClick:{
        type: Function,
        default () {
          return ()=>{}
        }
      },
      keyUpDebounce:{
        type: Number,
        default: 300
      },
      clearButton: {
        type: Boolean,
        coerce: coerceBoolean,
        default: false
      }
    },
    ready() {
      if (this.value.constructor !== Array) {
        if (this.value.length === 0) {
          this.value = []
        } else {
          this.value = [this.value]
        }
      } else {
        if (!this.multiple && this.value.length > 1) {
          this.value = this.value.slice(0, 1)
        } else if (this.multiple && this.value.length > this.limit) {
          this.value = this.value.slice(0, this.limit)
        }
      }

      var _this = this;
      document.addEventListener("click", function(){
        _this.show = false;
      });
    },
    data() {
      return {
        searchText: null,
        show: false,
        showNotify: false,
        isAllSelect: false
      }
    },
    computed: {
      selectedItems() {
        let foundItems = []
        if (this.value.length) {
          for (var item of this.value) {
            if (this.options.length ===0)
            {
              foundItems = this.value;
            }
            else
            {
              if (typeof item === "string") {
                let option
                this.options.some(o => {
                  if(o.value === item) {
                    option = o
                    return true
                  }
                })
                option && foundItems.push(option.label)
              }
            }
          }
          return foundItems.join(', ')
        }
      },
      showPlaceholder() {
        return this.value.length === 0
      }
    },
    watch: {
      'options': function () {
        if(this.options.length == 0 ||
          this.options.length != this.value.length) {
          this.isAllSelect = false;
        }
      }
    },
    methods: {
      checkLimit () {
        if (!this.multiple && this.value.length > 1) {
          this.value = this.value.slice(0, 1)
        } else if (this.multiple && this.value.length > this.limit) {
          this.showNotify = true
          this.value = this.value.slice(0, this.limit)
          setTimeout(() => this.showNotify = false, 1000)
        }
      },
      searchKeyUp() {
        if(this.isAjaxOption) {
          this.fetchOption(this.searchText);
        }
        
      },
      select(v) {
          if (this.value.indexOf(v) === -1) {
            if (this.multiple) {
              this.value.push(v)              
            } else {
              this.value = [v]
            }
          } else {
            if (this.multiple) {
              this.value.$remove(v)
            }
          }
          if(this.value.length === this.options.length && this.value.length != 0 ) {
            this.isAllSelect = true; 
          } else {
            this.isAllSelect = false;
          }
          if (this.closeOnSelect) {
            this.toggleDropdown()
          }
          if(typeof this.itemClick == 'function') {
            this.itemClick(v);
          }
          this.checkLimit();
      },
      isSelected(v) {
        if (this.value.constructor !== Array) {
          return this.value == v
        } else {
          return this.value.indexOf(v) !== -1
        }
      },
      toggleDropdown() {
        this.show = !this.show
        this.$dispatch('toggle-dropdown',this.show);
      },
      selectAll() {
        if(!this.isAllSelect) {
          let values = []
          this.options.forEach((item)=> {
            if(item.value != this.allSelectKey) {
              values.push(item.value)
            }
          })
          this.value = values
        } else {
          this.value = []
        }
        this.isAllSelect = !this.isAllSelect;
        this.checkLimit();
      },
      clear() {
        this.value = []
      }
    }
  }
// </script>

